@use '../../styles/index.scss' as *;

.uiKit {
  &.button {
    @extend %body-l;

    border: rem(2) solid transparent;
    outline: none;

    &.rectangle {
      padding: rem(8) rem(16);
      border-radius: rem(12);
    }

    &.square {
      padding: rem(12);
      border-radius: rem(16);
    }

    &.secondary {
      background-color: #f0f2f5; // hsla(216, 100%, 24%, 0.04);
      color: hsla(0, 0%, 0%, 0.72);
    }

    &.primary {
      background-color: $bg-action-primary;
      color: hsla(0, 0%, 100%, 0.92);
    }

    &.none {
      background-color: #f0f2f5; // hsla(216, 100%, 24%, 0.04);
      color: hsla(0, 0%, 0%, 0.72);
    }

    &.light {
      background-color: $bg-action-secondary;
      color: $txt-action;
    }

    &.strong {
      background-color: $bg-action-primary;
      color: hsla(0, 0%, 100%, 0.92);
    }

    &.icon {
      background-color: transparent;
      padding: 0;
      border: none;
      border-radius: rem(16);
    }

    &.disabled {
      cursor: not-allowed;

      &.secondary {
        color: rgba(0, 0, 0, 0.26);
        border: rem(1) solid rgba(0, 0, 0, 0.12);
      }

      &.primary {
        color: rgba(0, 0, 0, 0.26);
        box-shadow: none;
        background-color: rgba(0, 0, 0, 0.12);
      }
    }
  }
}
