// @todo Define typography
@use 'sass:map';

$FONT_WEIGHT_NORMAL: 400;

$title-fonts: (
  level-1: (font-family: GT Eesti Pro Display,
    font-size: rem(32),
    font-weight: 700,
    line-height: rem(36),
    letter-spacing: rem(0.4),

  ),
  level-3: (font-family: GT Eesti Pro Display,
    font-size: rem(24),
    font-weight: 700,
    line-height: rem(30),
    letter-spacing: rem(0.4),
  ),
  level-4: (font-family: GT Eesti Pro Display,
    font-size: rem(20),
    font-weight: 500,
    line-height: rem(26),
    letter-spacing: rem(0.4),
  ),
  level-4-accent: (font-family: GT Eesti Pro Display,
    font-size: rem(20),
    font-weight: 700,
    line-height: rem(26),
    letter-spacing: rem(0.4),
  ),
);

$caption-fonts: (
  small: (font-family: GT Eesti Pro Display,
    font-size: rem(12),
    font-weight: 400,
    line-height: rem(16),
    letter-spacing: rem(0.2),
  ),
  small-accent: (font-family: GT Eesti Pro Display,
    font-size: rem(12),
    font-weight: 500,
    line-height: rem(16),
    letter-spacing: rem(0.2),
  ),
);

$body-fonts: (
  l: ("font-size": 16px,
    "line-height": 20px,
    "letter-spacing": .2px,
    "font-weight": $FONT_WEIGHT_NORMAL,
  ),
  medium: (font-family: GT Eesti Pro Display,
    font-size: rem(14),
    font-weight: 400,
    line-height: rem(18),
    letter-spacing: rem(0.2),
  ),
  large: (font-family: GT Eesti Pro Display,
    font-size: rem(16),
    font-weight: 400,
    line-height: rem(20),
    letter-spacing: rem(0.2),

  ),
  large-accent: (font-family: GT Eesti Pro Display,
    font-size: rem(16),
    font-weight: 500,
    line-height: rem(20),
    letter-spacing: rem(0.2),
  ),
  400-small: (font-family: GT Eesti Pro Display,
    font-size: rem(14),
    font-weight: 400,
    line-height: rem(20),
    letter-spacing: rem(0.2),
  ),
  300-x-small: (font-family: GT Eesti Pro Display,
    font-size: rem(12),
    font-weight: 400,
    line-height: rem(16),
    letter-spacing: rem(0.2),
  ),
);

$body-compact-fonts: (
  control-500-medium: (font-family: GT Eesti Pro Display,
    font-size: rem(16),
    font-weight: 500,
    line-height: rem(20),
    letter-spacing: rem(0.2),
  ),
  control-400-small: (font-family: GT Eesti Pro Display,
    font-size: rem(14),
    font-weight: 500,
    line-height: rem(16),
    letter-spacing: rem(0.2),
  ),
  400-large: (font-family: GT Eesti Pro Display,
    font-size: rem(14),
    font-weight: 400,
    line-height: rem(18),
    letter-spacing: rem(0.2),
  )
);

$headline-fonts: (
  400-small: (font-family: GT Eesti Pro Display,
    font-size: rem(16),
    font-weight: 700,
    line-height: rem(20),
    letter-spacing: rem(0.2),
  ),
);

@function get-class-name($type, $size) {
  @return #{$type}-#{$size};
}

@each $prop, $values in $body-fonts {
  %#{get-class-name(body, $prop)} {
    font-weight: map.get($values, "font-weight");
    font-size: map.get($values, "font-size");
    line-height: map.get($values, "line-height");
    letter-spacing: map.get($values, "letter-spacing");
  }
}

@each $prop, $values in $headline-fonts {
  %#{get-class-name(headline, $prop)} {
    font-size: map.get($values, 'font-size');
    font-weight: map.get($values, 'font-weight');
    line-height: map.get($values, 'line-height');
    letter-spacing: map.get($values, 'letter-spacing');
  }
}

@each $prop, $values in $body-compact-fonts {
  %#{get-class-name(body-compact, $prop)} {
    font-size: map.get($values, 'font-size');
    font-weight: map.get($values, 'font-weight');
    line-height: map.get($values, 'line-height');
    letter-spacing: map.get($values, 'letter-spacing');
  }
}

@each $prop, $values in $caption-fonts {
  %#{get-class-name(caption, $prop)} {
    font-size: map.get($values, 'font-size');
    font-weight: map.get($values, 'font-weight');
    line-height: map.get($values, 'line-height');
    letter-spacing: map.get($values, 'letter-spacing');
  }
}

@each $prop, $values in $title-fonts {
  %#{get-class-name(title, $prop)} {
    font-size: map.get($values, 'font-size');
    font-weight: map.get($values, 'font-weight');
    line-height: map.get($values, 'line-height');
    letter-spacing: map.get($values, 'letter-spacing');
  }
}